@media screen and (max-width: 960px) {
  .icon {
    position: absolute;
    right: 20px;
    top: 20px;
    color: black;
    /* width: 200px; */
    /* height: auto; */
    transition: 0.5s;
  }

  .icon-rotate {
    position: absolute;
    right: 20px;
    top: 20px;
    color: black;
    transition: 0.5s;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 2rem;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 60px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: white;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .link {
    font-size: 2rem;
  }
}

